@import url('https://fonts.cdnfonts.com/css/wotfard');

body{
    box-sizing:border-box;
    margin: 0;
    padding: 0;
    height: 120vh;
    font-family: "wotfard";
}


/* Project container */
.container{
    width: 100%;
    max-width: 480px;
    height: 66px;
    margin: 0.1em 0px 0.3em 0px;
    
    border-radius: 5px;
    position: relative;
    display: flex;
    align-items: center;
    cursor: pointer;
    border-left: 5px solid transparent;
    background-color: rgb(250, 215, 215);
    
    
    
}

.project-holder{
    margin: 0;
    padding: 0;
    width:100%;
    display:flex;
    justify-content: center;
    



}



.container:hover{
    border-left: 5px solid rgb(131, 131, 131);
    width: 100%;
    height: 66px;
    
    
}

#tally{
    position: absolute;
    right: 0.5em;
    bottom: 1.2rem;
    font-size: 20px;
}

#project-name{
    margin-left: 2em;
    font-size: 16px;
    font-weight: bolder;
}



/* add project */

.add-button-proj{
    width: 100%;
    max-width: 480px;
    height: 66px;
    background-color: rgb(225, 225, 225);

    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 66px;
    cursor: pointer;
    border: 1px solid black;
    /* transition-property: width; */
    transition-duration: 0.1s;

}

.add-button-proj-clicked{
    background-color: red;
}

.add-button-proj:hover{
    background-color: rgb(185, 181, 181);
    color: white;
    height: 100px;
}

.project-container{
    width: 100%;
    height: 80px;
    display: flex;
    padding-left: 0px;
    justify-content: center;
    

}

.project-set{
    background-color: rgb(255, 230, 230);
    border: 2px solid black;
    border-radius: 5px;
    padding: 5px;
    width: 100%;
    max-width: 480px;
    cursor: pointer;

    transition-duration: 0.1s;
    /* transition-delay: 1s; */


}


/* outer */

.outer{
    display: flex;
    gap: 5px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* border: 2px solid black; */
    
    
}

.project-ul{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* border: 2px solid black; */
    padding-left: 0;
    

}

  .activeClass{
    border-left: 5px solid black;
  }

  .activeClass:hover{
    border-left: 5px solid black;
  }

/* Top Bar */

.top-div{
    display: flex;
    justify-content: center;
}
.top-div button{
    border: 0.1px solid black;
    padding: 10px 0;
    width: 100%;
    max-width: 160px;
    /* min-width: 200px; */
    /* max-width: 200px; */
    font-family: "wotfard";

    background-color: tomato;
    cursor: pointer;
}

.top-div button:hover{
    background-color: rgb(220, 92, 69);
}

.active-top{
    background-color: black !important;
    color: white;
}


/* Delete Button */

.delete{
    background-color: black;
    color: white;
    border-radius: 5px;
    border: none;
    padding: 2px 1px;
    position: absolute;
    right: 0;
    top: 0;
    cursor: pointer;
}

/* projectName */

#projectName{
    height: 1px;
}
/* NavBar */

.navBar{
    margin-top: 0;
    height: 3em;
    gap: 1em;
    display: flex;
    list-style: none;
    justify-content: flex-end;
    align-items: center;
    font-size: 22px;
    cursor: pointer;
    border-bottom: 1px solid black;
    background-color: #1F5156;
}


.navBar li{
    position: relative;
    right: 20px;

}

.navBar li a{
    position: relative;
    color: white;
    text-decoration: none;
    font-size: 40px;

}

.navBar li:hover{
    border-bottom: 1px solid #F5B922;
}

@media only screen and (max-width: 500px) {

    body{
        /* font-size: 2px !important; */
    }
    .top-div button {
        /* background-color: red; */
        width: 24.5%;
        font-size: 13px;
    }
    .container{
        font-size: 13px;
       
        width: 95%;
    }
    .container:hover{
        width: 95%;
        
    }

    .add-button-proj{
        width: 95%;
    }

    .project-set{
        width: 95%;
    }
}
